	module.exports = {"data":{"site":{"siteMetadata":{"title":"weiran.co","author":"Weiran Zhang"}},"markdownRemark":{"id":"/Users/weiran/git/weiran/weiran.co/src/pages/blog/2013/1/imagining-a-5-inch-iphone/index.md absPath of file >>> MarkdownRemark","html":"<p>Rene Ritchie at iMore imagining how a larger iPhone would work.</p>\n<p>If Apple were to make a larger iPhone, I would guess a 4.8-inch screen with a 272ppi. I’m still not convinced though, I see no advantage over the existing iPhone 5 that makes the added bulk and weight worthwhile. But as Rene suggests:</p>\n<blockquote>\n<p>If people are buying massive amounts of premium large-sized handsets, Apple could one day decide they’d rather those people buy premium Apple-made large-sized handsets than competing brands. If Apple sees value in increasing the scope of their addressable market, and thinks it will help them make a better product and sell more units, then that’s why Apple would add a bigger iPhone to the mix.</p>\n</blockquote>","frontmatter":{"title":"Imagining a 5-inch iPhone","date":"January 29, 2013","passthroughUrl":"http://www.imore.com/imagining-5-inch-iphone"}}},"pathContext":{"slug":"/blog/2013/1/imagining-a-5-inch-iphone/","previous":{"fields":{"slug":"/blog/2013/1/in-defense-of-apple-battling-the-mounting-hysteria/"},"frontmatter":{"title":"In defense of Apple: battling the mounting hysteria","passthroughUrl":null,"type":null}},"next":{"fields":{"slug":"/blog/2013/1/new-apple-tv-with-a5x-revealed-in-fcc-documents/"},"frontmatter":{"title":"New Apple TV with A5X revealed in FCC documents","passthroughUrl":"http://www.anandtech.com/show/6709/slightly-smaller-apple-tv-32-a1469-contains-a5x-soc-bcm4334-combo","type":null}}}}

